<template>
	<view class="content">
		<van-cell title="驳回意见" :value="rejectReason"></van-cell>
		<uni-section title="驳回意见" type="line">
			<uni-card :is-shadow="false">
				<text class="uni-body">{{ rejectReason }}</text>
			</uni-card>
		</uni-section>
		<view class="flex flex-direction" style="padding:30rpx">
			<button class="cu-btn bg-blue lg" @click="cancleDispatch">取消派发</button>
			<button class="cu-btn bg-green lg" style="margin-top: 20rpx;" @click="reDispatch">重新派发</button>
		</view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			rejectReason: '',
			id: '',
			grid: ''
		};
	},
	onLoad(options) {
		this.id = options.id;
		this.grid = options.gridId;
		if (this.id && this.grid) {
			this.getGridAdvice(this.id, this.grid);
		}
	},
	methods: {
		getGridAdvice(id, grid) {
			taskAPI.getGridAdvice({ id, grid }).then(res => {
				if (res.code == 200) {
					this.rejectReason = res.data.advice;
				}
			});
		},
		cancleDispatch() {
			//取消派发
			uni.showModal({
				content: '是否确定取消派发？',
				success: rs => {
					if (rs.confirm) {
						let param = {
							id: this.id,
							oldGridPolice: this.grid
						};
						taskAPI.cancelAssign(param).then(res => {
							if (res.code == 200) {
								uni.showModal({
									content:'取消成功,是否重新派发？',
									success:rs=>{
										this.reDispatch()
									},
									fail:rs=>{
										uni.navigateBack();
									}
								})
							}
						});
					}
				}
			});
		},
		reDispatch() {
			//重新派发
			uni.navigateTo({
				url: `../patrolTask/reDispatch?id=${this.id}&oldGridPolice=${this.grid}`
			});
		}
	}
};
</script>

<style></style>
